import React, { Component } from 'react';
import '../../App.css';
import Loan from '../../dataClasses/Loan.js'

import LoanInfo from '../../dataClasses/LoanInfo';
// import Loan from '../../dataClasses/Loan';

import ReactCSSTransitionGroup from 'react-addons-css-transition-group';
import LoanInputInterface from '../Interfaces/LoanInputInterface';

class LoanInputController extends Component{

  constructor(props) {
    super(props);
    this.state = { 
      numberOfLoans: 1,
      loanInputs: [<LoanInputInterface key={1} loanID={1}/>],
      removeLoanEnabled: ''   // No value means that it is disabled
     };

     this.addNewLoan        = this.addNewLoan.bind(this);
     this.removeLastLoan    = this.removeLastLoan.bind(this);
     this.onGoButtonPressed = this.onGoButtonPressed.bind(this);
  }

  addNewLoan(){

    // Add loan to the list of loans
    var loanTemp = this.state.loanInputs.slice()
    loanTemp.push(<LoanInputInterface key={this.state.numberOfLoans + 1} loanID={this.state.numberOfLoans + 1}/>);

    this.setState({
      numberOfLoans: this.state.numberOfLoans + 1,
      loanInputs: loanTemp,
      removeLoanEnabled: 'enabled'
    });

  }

  removeLastLoan(){

    // Remove the last item in array
    var loanTemp = this.state.loanInputs.slice()
    loanTemp.pop(); 
    
    // We need to see if the remove button needs to be disabled or not...
    if(this.state.numberOfLoans - 1 === 1){
      // There's only one loan left. Disable the removal of loans..
      // Set State
      this.setState({
        numberOfLoans: this.state.numberOfLoans - 1,
        loanInputs: loanTemp,
        removeLoanEnabled: ''
      });
    } else {
      // We have more than one loan left, thus we will allow the user to 
      // remove loans.
      this.setState({
        numberOfLoans: this.state.numberOfLoans - 1,
        loanInputs: loanTemp
      });
    }
  }

  onGoButtonPressed(){

    // Get an array of all the loans
    var loans = [];
    for(var i = 1; i < this.state.loanInputs.length + 1; i++){
      var temp = this.state.loanInputs[i];

      var loanName = "Loan " + i;
      var principal = document.getElementById("principal" + i).value;
      var interestRate = document.getElementById("interest" + i).value;
      var minimumMonthlyPayment = document.getElementById("monthlyPayment" + i).value;
      
      var loan = new Loan(loanName, principal, interestRate, minimumMonthlyPayment);
      loans.push(loan);
    }

    var monthlyPayment = document.getElementById("monthlyPayment").value;
    this.props.goButtonPressed(monthlyPayment, loans);
  }

  render(){
    return(
      
      <div>

        <div className="panel panel-default">
          <div className="panel-heading">
            <h3 className="panel-title">Monthly Payment</h3>
          </div>
          <div className="panel-body">
            <p>Let us know how much money you can put towards paying off your loans each month.</p>

            <label htmlFor="monthlyPayment">Principal</label>
            <input type="number" min="1.00" className="form-control" id="monthlyPayment" maxLength="7" defaultValue="300"/>
            
          </div>
        </div>

        <ReactCSSTransitionGroup
          transitionName="fade"
          transitionEnterTimeout={500}
          transitionLeaveTimeout={300}>
          {this.state.loanInputs}
        </ReactCSSTransitionGroup>

        <div className="row">
          <div className="col-md-4 buttonControls">
            <button type="button" id="removeLastButton" disabled={!this.state.removeLoanEnabled} onClick={this.removeLastLoan} className="btn btn-danger">Remove Last Loan</button>
            <button type="button" onClick={this.addNewLoan} className="btn btn-primary">Add Another Loan</button>
          </div>
          <div className="col-md-4">
            
          </div>
          <div className="col-md-4 buttonControls">
            <button type="button" onClick={this.onGoButtonPressed} className="btn btn-success" id="goButton">Go!</button>
          </div>
        </div>
      </div>
    );
  }
}

export default LoanInputController;


