import React, { Component } from 'react';
import '../../App.css';


class Header extends Component{
    render() {

        return (
        <div className="App">
            <header className="App-header">
            <h1 className="App-title">Loan Calculator</h1>
            <p>Find the quickest and cheapest way to pay off your student loans!</p>
            </header>
            
            <p>When paying off student loans, it can be hard to figure out which loans to prioritize to pay off your loans 
            both as quickly and cheaply as possible. Enter in your loans' principals and interest rates to find the optimal
            way to pay them off!</p>
        </div>
        );
    }
}

export default Header;


