import React, { Component } from 'react';
import '../../App.css';
import ReactCSSTransitionGroup from 'react-addons-css-transition-group';

class LoanInputInterface extends Component{

  constructor(props) {
    super(props);
    this.state = { 
      loanID: this.props.loanID,
      principalInputID: "principal" + this.props.loanID,
      interestInputID: "interest" + this.props.loanID,
      monthlyPaymentID: "monthlyPayment" + this.props.loanID
     };
  }

  render(){
    return(

      <div className="panel panel-default">

        <div className="panel-heading">
          <h3 className="panel-title">Loan {this.state.loanID}</h3>
        </div>

        <div className="panel-body">
          <div className="row">

            <div className="col-md-4">
              <div className="form-group">
                <label htmlFor={this.state.principalInputID}>Principal</label>
                <input type="number" min="0.00" className="form-control" id={this.state.principalInputID} defaultValue="1000"/>
              </div>
            </div>

            <div className="col-md-4">
              <div className="form-group">
                <label htmlFor={this.state.interestInputID}>Interest Rate</label>
                <input type="number" min="0.00" className="form-control" id={this.state.interestInputID} defaultValue="3.5"/>
              </div>
            </div>

            <div className="col-md-4">
              <div className="form-group">
                <label htmlFor={this.state.monthlyPaymentID}>Minimum Monthly Payment</label>
                <input type="number" min="0.00" className="form-control" id={this.state.monthlyPaymentID} defaultValue="100"/>
              </div>
            </div>
          </div>
        </div>

      </div>
    );
  }
}

export default LoanInputInterface;


