'use strict';

class Loan {
    constructor(name, principal, interestRate, minimumMonthlyPayment){
        this.name = name;
        this.principal = principal;
        this.interestRate = interestRate;
        this.minimumMonthlyPayment = minimumMonthlyPayment;
    }
}

module.exports = Loan;


