import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import Modal from 'react-modal';

import './App.css';
import ReactCSSTransitionGroup from 'react-addons-css-transition-group';

import LoanInfo from './dataClasses/LoanInfo';
import Loan from './dataClasses/Loan';

import LoanInputController from './components/Controllers/LoanInputController';
import Header from './components/Interfaces/Header';
import GraphAnimation from './components/Interfaces/GraphAnimation';

const customStyles = {
  content : {
    top                   : '50%',
    left                  : '50%',
    right                 : 'auto',
    bottom                : 'auto',
    marginRight           : '-50%',
    transform             : 'translate(-50%, -50%)'
  }
};

class App extends Component{

  constructor() {
    super();

    this.state = {
      modalIsOpen: false
    };

    this.openModal = this.openModal.bind(this);
    this.afterOpenModal = this.afterOpenModal.bind(this);
    this.closeModal = this.closeModal.bind(this);
    this.calculateLoanPaymentPlan = this.calculateLoanPaymentPlan.bind(this);
  }

  openModal() {
    this.setState({modalIsOpen: true});
  }

  afterOpenModal() {
    // references are now sync'd and can be accessed.
    // this.subtitle.style.color = '#f00';
  }

  closeModal() {
    this.setState({modalIsOpen: false});
  }

  calculateLoanPaymentPlan(monthlyPayment, arrayOfLoans){

    // First, disable the go button so the user can't do another calculation while we've
    // got this one going.
    document.getElementById("goButton").disabled = true;

    // Show the modal
    this.openModal();

    // We've got to call a web worker to do this calculating in the background.
    // Let's do this. 👊💥

  }

  render() {
    return (
      <div>

        <Header/>

        <div>
          <Modal
            id="myModal"
            isOpen={this.state.modalIsOpen}
            onAfterOpen={this.afterOpenModal}
            /* onRequestClose={this.closeModal} */
             style={customStyles}
            contentLabel="Example Modal"
          >
          <div id="modalText">
            <h2>🏗🚧 We're working on it! 🚧🏗</h2>

            <br/>
            <p>This may take a few moments, but we'll be done crunching the numbers soon!</p>
            <br/>
            
            <GraphAnimation/>
          </div>
          </Modal>
        </div>

        <div className="container">         
          <LoanInputController goButtonPressed={this.calculateLoanPaymentPlan}/>
        </div>

      </div>
    );
  }
}

export default App;


